import React, { useEffect } from 'react'

function Destinations() {

    useEffect(()=>{
        const grid = document.getElementById('grid');

        let clientX,initOffset = 18;
        grid.addEventListener('touchstart',(e)=>{
            clientX = Math.round(e.touches[0].clientX);
        })
        
        grid.addEventListener('touchmove',(e)=>{
            let check = initOffset;
            let offset = Math.round(e.touches[0].clientX) - clientX;
            
            if(Math.abs(offset) >= 200){
                check = grid.getBoundingClientRect().left;
            }
        
            if(!isAllowed(check,offset)){
                return;
            }
        
            grid.style.transform = `translateX(${offset + initOffset}px)`
        })
        
        function isAllowed(check,offset){
            if((check >= 18 && offset > 0) || (check <= -838 && offset <0)){
                return false;
            }else {
                return true;
            }
        }
        
        grid.addEventListener('touchend',()=>{
            initOffset = grid.getBoundingClientRect().left;
        })        

        let tourDestinationsUrls = [
            "https://www.travelraasta.in/packages?&destination=Himachal",
            "https://www.travelraasta.in/packages?&destination=Gujarat",
            "https://www.travelraasta.in/packages?&destination=Goa",
            "https://www.travelraasta.in/packages?&destination=Mumbai",
            "https://www.travelraasta.in/packages?&destination=Jaipur",
            "https://www.travelraasta.in/packages?&destination=New%20Delhi"
        ]

        const destElems = document.querySelectorAll('.dest');
        destElems.forEach((dest,index)=>{
            dest.addEventListener('click',(e)=>{
                console.log('dest cli',index);
                window.location = tourDestinationsUrls[index];
            })
        })

    })

    return (
        <div className="container">
            <section id="destinations">
                <h1>Top Destinations</h1>
                <div id="grid">
                    <div className="grid-item dest">
                        <h2>Himachal</h2>
                    </div>

                    <div className="grid-item dest">
                        <h2>Gujarat</h2>
                    </div>

                    <div className="grid-item dest">
                        <h2>Goa</h2>
                    </div>
            
                    <div className="grid-item dest">
                        <h2>Mumbai</h2>
                    </div>

                    <div className="grid-item dest">
                        <h2>Jaipur</h2>
                    </div>
            
                    <div className="grid-item dest">
                        <h2>Delhi</h2>
                    </div>            
                </div>                        
            </section>            
        </div>
    )
}

export default Destinations
